<template>
	<div class="index-board">
		<div class="inner userIdent-inner">
			<h3>公司信息认证</h3>
			<div>
				 <Form :model="userIdent" label-position="left" :label-width="100" inline>
					<FormItem label="公司名称" class="w96">
						<Input v-model="userIdent.Name" placeholder="请输入公司名称"></Input>
					</FormItem><br/>
					<FormItem label="注册号码">
						<Input v-model="userIdent.Surname" placeholder="请输入注册号码"></Input>
					</FormItem>
					<FormItem label="注册地点">
						<Input v-model="userIdent.Sex" placeholder="请输入注册地点"></Input>
					</FormItem><br/>
					<FormItem label="注册资本">
						<Input v-model="userIdent.nationality" placeholder="请输入注册资本"></Input>
					</FormItem>
					<FormItem label="成立日期">
						<DatePicker style="width:100%" type="date" placeholder="成立日期" v-model="userIdent.BirDate"></DatePicker>
					</FormItem><br/>
					<FormItem label="企业唯一标识" class="w96">
						<Input search enter-button placeholder="企业唯一标识" />
					</FormItem>
				</Form>
				<div class="btn-con">
				    <Button type="primary" @click="backConfig()">
						<Icon type="ios-arrow-back"></Icon>
						上一步
					</Button>
					<Button type="primary" @click="goCompany()">
						下一步
						<Icon type="ios-arrow-forward"></Icon>
					</Button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				userIdent: {
                    Name: '',
                    Surname: '',
                    Sex: '',
					nationality:'',
					BirDate:'',
					Address:''
                },
			}
		},
		methods:{
			backConfig(){
				this.$router.push({path:'UserIdent'})
			},
			goCompany(){
				this.$router.push({path:'TokenSet'})
			}
		}
	}
</script>

<style scoped lang="stylus">
	.index-board
		padding: 50px 100px !important;
		height: 480px !important;
		h3
			font-size:26px;
			margin-bottom: 20px;
		.ivu-form-item
			min-width: 330px;
			.ivu-form-item-label
				font-size: 16px;
		.w96
			width: 96%;
		.btn-con
			display: flex;
			display: -webkit-flex;
			justify-content: space-between;
			align-items: center;
			margin-top: 50px;
</style>


